import { Component, OnInit, computed, signal } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonHeader,
  IonTitle,
  IonToolbar,
  IonButtons,
  IonButton,
  IonIcon,
  IonText,
  IonSearchbar,
  IonList,
  IonItem,
  IonListHeader,
  IonLabel,
  IonAccordion,
  IonAccordionGroup,
  IonSkeletonText,
  IonSelect,
  IonSelectOption,
  IonModal,
  IonCheckbox,
  IonRow,
  IonCol,
} from '@ionic/angular/standalone'
import { HeaderComponent } from 'src/app/components/header/header.component'
import {
  callOutline,
  filterCircleOutline,
  logoWhatsapp,
  mailOutline,
  searchOutline,
} from 'ionicons/icons'
import { addIcons } from 'ionicons'
import {
  faqLoadingState,
  SupportService,
} from 'src/app/services/api-wrappers/support.service'
import {
  RetrieveFaq200ResponseAllOfDataInner,
  RetrieveProgram200Response,
} from 'src/api-cyclist'
import Fuse from 'fuse.js'
import { Capacitor } from '@capacitor/core'
import { StorageService } from 'src/app/services/utils/storage.service'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import { Observable, map } from 'rxjs'

@Component({
  selector: 'app-support',
  templateUrl: './support.page.html',
  styleUrls: ['./support.page.scss'],
  imports: [
    IonContent,
    CommonModule,
    FormsModule,
    HeaderComponent,
    IonButton,
    IonIcon,
    IonText,
    IonSearchbar,
    IonList,
    IonItem,
    IonListHeader,
    IonLabel,
    IonAccordion,
    IonAccordionGroup,
    IonSkeletonText,
    IonModal,
    IonCheckbox,
    IonRow,
    IonCol,
    TranslateModule,
  ],
})
export class SupportPage implements OnInit {
  faqState: faqLoadingState
  searchQuery: string = ''
  skeletonCount: number = 6
  selectedCategories: Set<string> = new Set()
  private fuse: Fuse<RetrieveFaq200ResponseAllOfDataInner> | null = null
  public program = signal<RetrieveProgram200Response | null>(null)
  openingHoursText$: Observable<string>
  currentLang = signal(this.translate.currentLang)

  private formatTimeSlot(
    slot: { open: string; close: string } | undefined,
    format: string
  ): string {
    if (!slot || !slot.open || !slot.close) {
      return this.translate.instant('support.opening_hours.closed')
    }

    const openTime = this.formatTime(slot.open, format)
    const closeTime = this.formatTime(slot.close, format)
    return this.translate.instant('support.opening_hours.time_slot', {
      open: openTime,
      close: closeTime,
    })
  }

  private formatTime(time: string, format: string): string {
    if (!time) return ''
    const [hours, minutes] = time.split(':')
    return format.replace('HH', hours).replace('mm', minutes)
  }

  getDayHours = computed(() => {
    const program = this.program()
    const lang = this.currentLang()

    const format = this.translate.instant('support.opening_hours.format')
    const openingHours = program?.config?.hotline?.opening_hours?.weekdays

    if (!openingHours) {
      return []
    }

    const groups: { start: string; end: string; hours: string }[] = []
    let currentGroup: { start: string; end: string; hours: string } | null =
      null

    // Order days from Monday (1) to Sunday (0)
    const orderedDays = ['1', '2', '3', '4', '5', '6', '0']

    for (const dayKey of orderedDays) {
      const dayName = `support.opening_hours.days.${dayKey}`
      const slot = openingHours[dayKey]
      const hours = this.formatTimeSlot(slot, format)

      if (!currentGroup) {
        currentGroup = { start: dayName, end: dayName, hours }
      } else if (currentGroup.hours === hours) {
        currentGroup.end = dayName
      } else {
        groups.push(currentGroup)
        currentGroup = { start: dayName, end: dayName, hours }
      }
    }

    // Add the last group if it exists
    if (currentGroup) {
      groups.push(currentGroup)
    }

    return groups.map((group) => {
      if (group.start === group.end) {
        return `${this.translate.instant(group.start)} : ${group.hours}`
      }
      return `${this.translate.instant(
        'support.opening_hours.from'
      )} ${this.translate.instant(group.start)} ${this.translate.instant(
        'support.opening_hours.to'
      )} ${this.translate.instant(group.end)} : ${group.hours}`
    })
  })

  getHolidayHours = computed(() => {
    const program = this.program()
    const lang = this.currentLang()

    const format = this.translate.instant('support.opening_hours.format')
    const slot = program?.config?.hotline?.opening_hours?.holidays
    return this.formatTimeSlot(slot, format)
  })

  constructor(
    public supportService: SupportService,
    private storage: StorageService,
    private translate: TranslateService
  ) {
    addIcons({
      callOutline,
      logoWhatsapp,
      mailOutline,
      searchOutline,
      filterCircleOutline,
    })
    this.supportService.faqState.subscribe((state) => {
      this.faqState = state
      if (state.state === 'success') {
        this.fuse = new Fuse(state.faq, {
          keys: ['question', 'answer'],
          threshold: 0.3,
          ignoreLocation: true,
        })
      }
    })

    this.translate.onLangChange.subscribe((event) => {
      this.currentLang.set(event.lang)
    })
  }

  async ngOnInit() {
    const program = await this.storage.get('program')
    this.program.set(program)
    this.supportService.getFaq()
    this.supportService.faqState.subscribe((state) => {
      if (state.state === 'success') {
        this.selectedCategories = new Set([
          ...new Set(state.faq.map((item) => item.category)),
        ])
      }
    })

    const openingHours = this.program()?.config?.hotline?.opening_hours
    if (openingHours) {
      this.openingHoursText$ = this.getFormattedOpeningHours(openingHours)
    }
  }

  private getFormattedOpeningHours(openingHours: any): Observable<string> {
    return this.translate.get('support.opening_hours').pipe(
      map((translations) => {
        const formattedDays = []
        for (let i = 0; i <= 6; i++) {
          const dayKey = i.toString()
          const dayName = `support.opening_hours.days.${dayKey}`
          const slot = openingHours.weekdays[dayKey]
          formattedDays.push(
            `${this.translate.instant(dayName)} : ${this.formatTimeSlot(
              slot,
              translations.format
            )}`
          )
        }

        const holidays = `${this.translate.instant(
          'support.opening_hours.days.holidays'
        )} : ${this.formatTimeSlot(openingHours.holidays, translations.format)}`

        return [...formattedDays, holidays].join('\n')
      })
    )
  }

  async handleSearch(event: any) {
    this.searchQuery = event.target.value
  }

  toggleCategory(category: string, isChecked: boolean) {
    if (isChecked) {
      this.selectedCategories.add(category)
    } else {
      this.selectedCategories.delete(category)
    }
  }

  getFilteredFaq(): RetrieveFaq200ResponseAllOfDataInner[] {
    if (this.faqState.state !== 'success') return []

    let filtered = this.faqState.faq

    if (this.searchQuery && this.fuse) {
      filtered = this.fuse.search(this.searchQuery).map((result) => result.item)
    }

    filtered = filtered.filter((item) =>
      this.selectedCategories.has(item.category)
    )

    return filtered
  }

  getFaqByCategory(category: string) {
    if (this.faqState.state !== 'success') return []

    return this.getFilteredFaq().filter((item) => item.category === category)
  }

  getUniqueCategories(): string[] {
    if (this.faqState.state !== 'success') return []
    return [...new Set(this.faqState.faq.map((item) => item.category))]
  }

  isAllSelected(): boolean {
    return this.selectedCategories.size === this.getUniqueCategories().length
  }

  toggleAll(selectAll: boolean) {
    const categories = this.getUniqueCategories()
    if (selectAll) {
      categories.forEach((category) => this.selectedCategories.add(category))
    } else {
      this.selectedCategories.clear()
    }
  }

  scrollToFaq() {
    const element = document.getElementById('faq')
    if (element && Capacitor.isNativePlatform()) {
      element.scrollIntoView({ behavior: 'smooth' })
    }
  }
}
